import copy from 'fast-copy';
import { freezeSys, toPlainObject } from 'contentful-sdk-core';
import enhanceWithMethods from '../enhance-with-methods';
import * as endpoints from '../plain/endpoints';

function createUploadApi(http) {
  return {
    "delete": function del() {
      var raw = this.toPlainObject();
      return endpoints.upload.del(http, {
        spaceId: raw.sys.space.sys.id,
        uploadId: raw.sys.id
      });
    }
  };
}
/**
 * @private
 * @param {Object} http - HTTP client instance
 * @param {Object} data - Raw upload data
 * @return {Upload} Wrapped upload data
 */


export function wrapUpload(http, data) {
  var upload = toPlainObject(copy(data));
  var uploadWithMethods = enhanceWithMethods(upload, createUploadApi(http));
  return freezeSys(uploadWithMethods);
}